
.user-panel {
  position: absolute;
  bottom: 0;
  background-color: #d8d3c0;
  height: 40px;
  padding-bottom: 50px;
}
.user-panel .image img{
  border-radius: 10px;
}
#searchForm {
  padding-left: 0em;
  padding-right: 0em;
}
#searchContainer {
  height: 100%;
  padding-bottom: 0em;
}
#search {
  width: 80%;
  float: right
}
#search-btn {
  width: 20%;
}
.main-sidebar{
 margin-top: 5px;
  height: 100%; 
   -moz-box-shadow:    0px 0 6px 0 #555;
  -webkit-box-shadow:  0px 0 6px 0 #555;
  box-shadow:          0px 0 6px 0 #555;
}



