
.mx-input-append>svg {
    height: 20px;
}
.multiselect__element>span>span {
    color: rgba(41, 36, 36, 0.856)
}
.button-modal {
    background: white;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 10px;
    letter-spacing: 1px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    min-width: 140px;
    margin-top: 8px;
    color: #313233;
    cursor: pointer;
    border: 1px solid #bbbbbb;
    text-transform: uppercase;
    transition: 0.1s all;
    font-size: 13px;
    outline: none;
}
.btn-create:hover {
  border-color: #3fb0ac;
  color: #3fb0ac;
}
.btn-close:hover {
    border-color:  hsl(350, 47%, 47%);
    color: hsl(350, 47%, 47%);
}
.invalid-feedback{
  font-size: 12px;
  color: red;
  z-index: 9;
  position: absolute;
  margin-top: 35px;
}
.box-title2 {
    letter-spacing: 1px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    color: #313233;
    text-transform: uppercase;
    transition: 0.1s all;
    font-size: 16px;
    cursor: pointer;
}
.alert-in-enter-active {
    animation: bounce-in .5s;
}
.alert-in-leave-active {
    animation: bounce-in .5s reverse;
}
.sitLenMotTi{
    margin-top: 0px;
}
.title {
  padding: 6px 6px;
  font-size: 14px;
  font-weight: 600;
  margin-left: 0px;
  padding-left: 0px
}



